/* src/app/features/snippets/snippets-page/snippets-page.scss */
.snippets-container {
  max-width: 900px;
  margin: 0 auto;
  padding: 2rem 1rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.snippet-form {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  align-items: flex-end;
}
.snippet-form mat-form-field {
  flex: 1 1 200px;
  min-width: 180px;
}
.code-field {
  flex: 2 1 400px;
  min-width: 300px;
}
.form-actions {
  display: flex;
  gap: 1rem;
  align-items: center;
}
.form-actions-block {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  gap: 1rem;
  margin-top: 1rem;
}
.snippet-search-filter {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}
.snippets-list {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.snippet-card {
  background: #fafafa;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.04);
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  position: relative;
}
.snippet-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 1rem;
  position: relative;
}
.snippet-title {
  font-size: 1.2rem;
  font-weight: 600;
  color: #222;
}
.snippet-meta {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 0.5rem;
}
.snippet-lang {
  font-size: 0.95rem;
  color: #3f51b5;
  font-weight: 500;
}
.snippet-tags mat-chip-list {
  margin-top: 0.25rem;
}
pre {
  background: #23272e;
  color: #f8f8f2;
  border-radius: 6px;
  padding: 1rem;
  font-size: 1rem;
  overflow-x: auto;
  margin: 0;
}
.snippet-actions {
  display: flex;
  gap: 0.5rem;
  position: static;
  margin-left: 1rem;
  align-items: center;
}
.no-snippets {
  text-align: center;
  color: #888;
  font-size: 1.1rem;
  margin-top: 2rem;
}
@media (max-width: 700px) {
  .snippets-container {
    padding: 1rem 0.25rem;
  }
  .snippet-form,
  .snippet-search-filter {
    flex-direction: column;
    gap: 0.5rem;
  }
  .snippet-actions {
    position: static;
    margin-top: 1rem;
    justify-content: flex-end;
  }
  .form-actions-block {
    flex-direction: column;
    align-items: stretch;
    gap: 0.5rem;
    margin-top: 0.7rem;
  }
}
.feature-card {
  max-width: 900px;
  margin: 2.5rem auto 0 auto;
  background: #fff;
  border-radius: 18px;
  box-shadow: 0 4px 24px 0 rgba(0, 0, 0, 0.08), 0 1.5px 6px 0 rgba(0, 0, 0, 0.04);
  padding: 2.5rem 2.5rem 2rem 2.5rem;
  animation: fadeIn 0.7s cubic-bezier(0.4, 0, 0.2, 1);
}
.feature-card-header {
  display: flex;
  align-items: center;
  gap: 0.9rem;
  margin-bottom: 1.5rem;
}
.feature-card-header mat-icon {
  font-size: 2.2rem;
  height: 2.2rem;
  width: 2.2rem;
  min-width: 2.2rem;
  min-height: 2.2rem;
  color: #1976d2;
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 1;
  vertical-align: middle;
  position: relative;
  top: 0;
}
.feature-card-header h1 {
  font-size: 1.7rem;
  font-weight: 700;
  margin: 0;
  color: #222;
}
@media (max-width: 700px) {
  .feature-card {
    padding: 1.2rem 0.7rem 1.2rem 0.7rem;
    max-width: 98vw;
  }
  .feature-card-header h1 {
    font-size: 1.1rem;
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: none;
  }
}
.snippet-form-row {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  align-items: flex-end;
}
.snippet-form-row mat-form-field {
  flex: 1 1 200px;
  min-width: 180px;
}
@media (max-width: 700px) {
  .snippet-form-row {
    flex-direction: column;
    gap: 0.5rem;
  }
}
.snippet-lang-chip {
  background: #e3e8f0;
  color: #1976d2;
  font-weight: 700;
  font-size: 1rem;
  margin-bottom: 0.2rem;
  margin-right: 0.7rem;
  height: 2rem;
  display: inline-flex;
  align-items: center;
  padding: 0 1rem;
  border-radius: 16px;
  border: 1.5px solid #b6c6e3;
  box-shadow: 0 1px 4px rgba(25, 118, 210, 0.07);
  letter-spacing: 0.03em;
  transition: background 0.2s, color 0.2s;
}
/*# sourceMappingURL=snippets-page.css.map */
